import Bus  from './bus'
import Aux from '../common/auxx'
import VPrintTemplate from './printTemplate'
export default {
    name :'VPrintTemplate',
    components :{ VPrintTemplate },
    props :{
        // JOSN
        value :{
            type: [Object, Array],
        },
        // 
        note :{
            type :[Boolean],
            default :false
        },
        // 
        unote :{
            type :[Boolean],
            default :false
        },
        // 
        aone :{
            type :[Boolean],
            default :false
        },
        // data ,(data ); 
        udata :{
            type :[Boolean],
            default :false
        },
        // data
        idata :{
            type :[Boolean],
            default :true
        },
        // data
        odata :{
            type :[Boolean],
            default :false
        },
        //  
        mock :{
            type :[Boolean],
            default :false
        },
        // 
        overflow :{
            type:[Boolean],
            default :true
        }
    },
    data() {
        return {
            list :[],
            parent :{},
            mock_mode :[
                {label: '',value: 'default'},
                {label: '',value: 'high'},
            ],
            data_status :[
                { label :'' ,value :'default' },
                { label :'' ,value :'optional' },
                { label :'' ,value :'not' }
            ]
        }
    },
    created(){
        this.list = this.value.list || [];
        this.parent = this.value.parent;
        if(this.aone){
            this.list = Aux.aone(this.list);
        }
    },
    methods :{
        getMockMenu :function(){
            return Aux.mockRandom();
        },
        getDataStatusStyle :function(state){
            var dataStyle = { };
            switch(state){
                case 'optional' :
                        dataStyle = {
                            color : '#027BE3',
                        }
                    break;
                case 'not' :
                        dataStyle = {
                            color : '#9e9e9e',
                            textDecoration :'line-through'
                        }
                    break;
                default :
                        dataStyle = {
                            color : '#0b7500',
                        }
                    break;
            }
            return dataStyle;
        },
        tapKey :function(item){
            Bus.$emit('tapKey',JSON.parse(JSON.stringify(item)))
        },
        
    }

}
