export default {
    /**
     * Aux                /(name:'name')
     * id       
     * key      
     * data             objectobjectchildren 
     * pid      id
     * ptype    
     * type     
     * chlidren   
     *
     *  
     * note     
     * data_status date  1 2 3
     * mock_random : 
     * mock_mode :      //  1 2
     * mock_rule : 
     * mock_value :          
     */
    getParent :function(aTree,id ,pid ,ptype ,parent){
        parent = parent || null;
        ptype = ptype || null;
        id = id || 0;
        pid = pid || 0;
        var aParent =[];
        for(var key in aTree){
            ++id ;
            var aux = { id ,pid ,key }     
            var analysis = this.initAnalysis(aTree[key]);
            aux.type = analysis.type;
            aux.ptype = ptype;
            aux.parent = parent;
            //  v-model defalut 
            aux.note = "";
            aux.mock_random ="";
            aux.mock_mode = "default";
            aux.data_status = 'default';
            aux.mock_rule ="";
            aux.mock_value = "";
            // 
            if(analysis.isChild){
                var newParent = { }
                newParent.key = aux.key;
                newParent.id = aux.id;
                newParent.ptype = aux.ptype;
                newParent.pid = aux.pid;
                newParent.type = aux.type;
                aux.children = this.getParent(aTree[key], id ,id ,analysis.type ,newParent);
            }else{
                aux.data = aTree[key];
            }
            aParent.push(aux);
        } 
        return {
            list  :aParent,
            parent,
        };
    },
    /**
     *  
     * @param {Object} value 
     */
    initAnalysis :function(value){
        var type = Object.prototype.toString.call(value);
        var isChild = false;
        var newType = 'String';
        switch(type){
            case '[object Object]' :
                isChild = true;
                newType = 'Object';
            break;
            case '[object Array]' :
                if(Object.prototype.toString.call(value[0]) == '[object Object]'){
                    isChild = true;
                    newType = 'ArrayObject';
                }else{
                    isChild = false;
                    newType = 'Array';
                }
            break;
            case '[object String]' :
                isChild = false;
                newType = 'String';
            break;
            case '[object Number]' :
                isChild = false;
                newType = 'Number';
            break;
            case '[object Boolean]' :
                isChild = false;
                newType = 'Boolean';
            break;
        }
        return {
            type :newType,
            isChild
        };
    },
    /**
     * data
     * @param {*} aTree 
     */
    clear :function(aTree){
        for(var key in aTree){ 
            var analysis = this.initAnalysis(aTree[key]);
            if(key == "data"){
                aTree['data'] = '';
            }
            if(analysis.isChild){
                this.clear(aTree[key]);
            }
        }
        return aTree
    },
    /**
     * json 
     * @param {*} aTree 
     */
    aone :function(aTree){
        for(var key in aTree){ 
            var analysis = this.initAnalysis(aTree[key]);
            if(aTree["type"] == "ArrayObject" && aTree["children"] && aTree["children"].list){
                aTree["children"].list = aTree["children"].list.slice(0,1)
            }
            if(analysis.isChild){
                this.aone(aTree[key]);
            }
        }
        return aTree
    },
    /**
     *  json
     * key   
     * data 
     * { keydata }  aux key  
     * @param {*} aTree  
     */
    inverseCompilation :function(aTree,attr,isOA){
        var naux = { };
        var aaux =[];
        if(!isOA){  //
            for(let i = 0; i<aTree.length; i++){
                let atre = aTree[i];
                let atre_children = atre["children"];
                let attr_key = atre[attr.key];
                let attr_data = atre[attr.data];
                if(!atre_children){
                    naux[attr_key] = this.conversion(attr_data);
                }else{
                    if(atre["type"] == "ArrayObject"){
                        naux[attr_key] = this.inverseCompilation(atre_children['list'],attr,true) 
                    }else{
                        naux[attr_key] = this.inverseCompilation(atre_children['list'],attr) 
                    }
                }
            }
        }else{
            for(let item in aTree){
                let alist = aTree[item]['children']['list'];
                for(let it in alist){
                    let atre = alist[it];
                    let atre_children = atre["children"];
                    let attr_key = atre[attr.key]
                    let attr_data = atre[attr.data]
                    if(!atre_children){
                        naux[attr_key] = this.conversion(attr_data);
                    }else{
                        if(atre['type'] == "ArrayObject"){
                            naux[attr_key]  = this.inverseCompilation(atre_children['list'],attr,true) 
                        }else{
                            naux[attr_key]  = this.inverseCompilation(atre_children['list'],attr) 
                        }
                    }
                }
                aaux.push(naux);
                naux = {};
            }
        }
        return isOA ? aaux : naux;
    },
    inverse :function(aTree){
        return this.inverseCompilation(aTree,{ key:'key' ,data :'data' },)
    },
    /**
     * mock
     * Aux
     */
    mock :function(aTree ,isOA){
        var naux = { };
        var aaux =[];
        var mkey = '';
        var mdata = '';
        var key = 'key';
        var rule = 'mock_rule';
        var mode = 'mock_mode';     
        var value ='mock_value';
        var random = 'mock_random';

        if(!isOA){  //
            for(let i = 0; i<aTree.length; i++){
                let atre = aTree[i];
                let atre_children = atre["children"];
                if(atre[mode] !== 'default'){
                    mkey = atre[key] +'|' +atre[rule];
                    mdata = atre[value];
                }else{
                    mkey = atre[key];
                    mdata = atre[random];
                }
                if(!atre_children){
                    naux[mkey] = this.conversion(mdata);
                }else{
                    if(atre["type"] == "ArrayObject"){
                        naux[mkey] = this.mock(atre_children['list'],true) 
                    }else{
                        naux[mkey] = this.mock(atre_children['list']) 
                    }
                }
            }
        }else{
            for(let item in aTree){
                let alist = aTree[item]['children']['list'];
                for(let it in alist){
                    let atre = alist[it];
                    let atre_children = atre["children"];

                    if(atre[mode] !== 'default'){
                        mkey = atre[key] +'|' +atre[rule];
                        mdata = atre[value];
                    }else{
                        mkey = atre[key];
                        mdata = atre[random];
                    }

                    if(!atre_children){
                        naux[mkey] = this.conversion(mdata)
                    }else{
                        if(atre['type'] == "ArrayObject"){
                            naux[mkey]  = this.mock(atre_children['list'],true) 
                        }else{
                            naux[mkey]  = this.mock(atre_children['list']) 
                        }
                    }
                }
                aaux.push(naux);
                naux = {};
            }
        }
        return isOA ? aaux : naux;
    },
    /**
     * mockrandom    12
     */
    mockRandom :function(){    
        return mock_random;
    },
    conversion :function(value){
        var newVal = value;
        if(typeof value ==='string'){
            try {
                newVal = JSON.parse(value);
            } catch (e) {
                if(value == "true"){ 
                    value = true;
                }else if(value == "false"){
                    value = false;
                }else{
                    let n = Number(value); 
                    if(!isNaN(n)){
                        value = n;
                    }
                }
            }
        }
        return newVal;
    },
    getParentTree :function(aTree,arr){
        arr = arr || []
        if(aTree.list){
            aTree.list.forEach(element => {
                if(element.children){
                    this.getParentTree(element.children,arr)
                    delete element.children
                    arr.push(element)
                }else{
                    arr.push(element)
                }
            });
        }
        return arr;
    }
}





